@use "base/variables" as v;
@use "base/mixins" as m;

.register {
    h3 {
        color: v.$primaryBlue;
        text-align: center;
        margin-top: 3rem;
        font-size: 2.5rem;
        @include m.laptop {
            margin-top: 7rem;
        }
        @include m.desktop {
            font-size: 6rem;
        }
    }
    p {
        padding-left: 2rem;
        padding-right: 2rem;
        font-family: v.$text;
    }
    .contact-form {
        border: 1px solid v.$primaryBlue;
        margin: 2rem;
        @include m.tablet {
            width: 80%;
            margin: 7rem auto;
        }
        @include m.desktop {
            width: 50%;
            margin: 8rem auto 12rem;
        }
    }
    form {
        padding: inherit 2rem;
        padding-right: 3rem;
        label {
            padding-top: 1rem;
            font-family: v.$text;
            font-size: 1.5rem;
            padding-bottom: 1rem;
            text-align: center;
            color: v.$primaryBlue;
            @include m.mobile {
                text-align: left;
            }
            @include m.tablet {
                font-size: 1.5rem;
            }
            @include m.laptop {
                font-size: 1.75rem;
                padding-left: 1rem;
            }
        }
        .label-form {
            width: 100%;
        }
        .form-check {
            .form-check-label {
                @include m.laptop {
                    padding-left: 0;
                }
            }
            .form-check-input {
                margin-top: 1rem;
                @include m.laptop {
                    margin-top: 1.2rem;
                    margin-left: 0.1rem;
                }
                @include m.desktop {
                    margin-top: 1.4rem;
                }
                @include m.desktopXL {
                    margin-top: 1.5rem;
                }
            }
            margin: 1rem auto 2rem;
            label {
                p {
                    padding: 0;
                }
            }
        }
        .form-btn {
            width: 100%;
            button {
                width: 100%;
                @include m.tablet {
                    width: unset;
                }
            }
        }
    }
    .form-options {
        margin-top: 2rem;
        margin-bottom: 2rem;
        margin-left: 1rem;
        a {
            display: block;
            color: v.$secondaryGray;
            text-align: left;
            margin: 0;
            padding: 0;
            &:hover {
                color: v.$primaryBlueDark;
            }
        }
        @include m.mobile {
            font-size: 1.2rem;
        }
        @include m.tablet {
            font-size: 1.5rem;
        }
        @include m.laptop {
            margin: 2rem 2.5rem;
        }
    }
}