@use "base/variables" as v;
@use "base/mixins" as m;

.form-body {
    .contact-form {
      padding: 0.5rem;
      .contact-form-title {
        text-align: center;
        margin: 2rem 1rem 1rem 1rem;
        @include m.laptop {
          margin: 3rem;
          font-size: 2.5rem;
          color: v.$primaryBlue;
          // padding-top: 4rem;
        }
        @include m.desktop {
          font-size: 3rem;
          margin: 1rem auto 2rem auto;
        }
        @include m.desktopXL {
          padding-top: 0;
          padding-top: 3rem;
          font-size: 3.5rem;
        }
      }
    }
  }
  
  .program-contact {
    background-color: lighten($color: v.$primaryBlue, $amount: 50);
    @include m.tablet {
      width: 70%;
      margin: 2rem auto;
    }
    @include m.desktop {
      width: 50%;
      margin: 2rem auto;
    }
    @include m.desktopXL {
      width: 40%;
      margin-bottom: 10rem;
    }
    h2 {
        color: v.$primaryBlue;
    }
  }
  
  form {
     p {
        margin: unset;
     }
    padding: 2.5rem 2rem;
    @include m.mobile {
      padding: 2.5rem 4rem;
    }
    @include m.tablet {
      padding: 2.5rem 3rem;
    }
    @include m.desktop {
      padding: 2.5rem 5rem;
    }
    .form-control::placeholder,
    select,
    .form-select,
    option {
      color: v.$primaryBlueDark;
    }
    .form-control,
    select {
      border: 1px solid v.$primaryBlueDark !important;
      border-radius: 1rem !important;
    }
    .aviso {
      padding-top: 0.5rem;
      color: v.$primaryBlueDark;
    }
    .btn-send {
      background-color: v.$primaryBlueDark;
      color: v.$white;
      text-transform: uppercase;
      //   font-family: v.$text;
      padding: 0.5rem 3rem;
      border-radius: 2rem;
      transition: all 0.5s;
      &:hover {
        // background-color: darken($color: v.$primaryBlueDark, $amount: 5);
        // color: v.$white;
        transition: all 0.5s;
      }
      @include m.tablet {
        padding: 1rem 6rem;
      }
    }
    .form-control,
    .form-select {
      background-color: transparent;
      padding: 0.75rem;
      @include m.laptop {
        padding: 1.5rem;
        margin: 0.5rem;
      }
      @include m.desktop {
        padding: 0.75rem;
      }
      @include m.desktopXL {
        padding: 1.5rem;
      }
      //   font-family: v.$textNormal;
      //   color: v.$primaryBlue;
      &:focus {
        border-color: v.$primaryBlueDark;
      }
    }
    //   label {
    //       color: v.$primaryBlue;
    //   }
    a {
      //   color: v.$primaryBlue;
      &:hover {
        // color: v.$primaryBlueDark;
      }
    }
  }
  
  .form-check-input {
    width: 1.5rem;
    height: 1.5rem;
    margin-right: 1rem;
  }
  
  .form-control.is-invalid,
  .was-validated .form-control:invalid {
    border-color: transparent;
    background-image: none;
    padding-right: 1rem;
  }
  
  .form-select.is-invalid:not([multiple]):not([size]),
  .form-select.is-invalid:not([multiple])[size="1"],
  .was-validated .form-select:invalid:not([multiple]):not([size]),
  .was-validated .form-select:invalid:not([multiple])[size="1"],
  .form-select.is-valid:not([multiple]):not([size]),
  .form-select.is-valid:not([multiple])[size="1"],
  .was-validated .form-select:valid:not([multiple]):not([size]),
  .was-validated .form-select:valid:not([multiple])[size="1"] {
    padding-right: 1rem;
    border-color: transparent;
    //   background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='M2 5l6 6 6-6'/%3e%3c/svg%3e");
  }
  
  .invalid-feedback {
    padding-left: 0.5rem;
    // font-family: v.$text;
  }
  
  .form-control.is-valid,
  .was-validated .form-control:valid {
    border-color: transparent;
    padding-right: 1rem;
    background-image: none;
  }
  
  .form-check {
    margin-left: 0.5rem;
  }
  
  .form-check-input {
    margin-top: 0.5rem;
  }
  
  .form-check-input:checked {
    background-color: v.$primaryBlueDark;
    border-color: v.$primaryBlueDark;
  }
  
  .form-control.is-valid,
  .was-validated .form-control:valid {
    border-color: v.$primaryBlueDark;
  }
  
  .form-check-input.is-valid:checked,
  .was-validated .form-check-input:valid:checked,
  .form-check-input.is-valid,
  .was-validated .form-check-input:valid {
    background-color: v.$primaryBlueDark;
    border-color: v.$primaryBlue;
  }