@use "base/variables" as v;
@use "base/mixins" as m;

footer {
  background-color: v.$primaryBlueDark;
  .footer-grid {
    display: grid;
    grid-template-columns: 1fr;
    padding-top: 2rem;
    @include m.tablet {
      grid-template-columns: repeat(2, 1fr);
    }
    @include m.laptop {
      padding: 0;
      margin: 5rem 4rem 0 4rem;
    }
    @include m.desktop {
      margin: 5rem 5rem 5rem 5rem;
      grid-template-columns: repeat(3, 1fr);
    }
    @include m.desktopXL {
      margin: 2rem 7.5rem;
    }
    .form-info {
      display: flex;
      align-items: center;
      margin: 2rem;
      .img-container {
        display: none;
        width: 4rem;
        @include m.tablet {
          display: flex;
          justify-content: center;
        }
        @include m.laptop {
          width: 4.5rem;
        }
        @include m.desktop {
          width: 4rem;
        }
        padding: 0.5rem;
        img {
          width: 100%;
        }
      }
      .info-text {
        p {
          color: v.$white
        }
        color: v.$white;
        margin-left: 1rem;
        // font-size: 1.75rem;
        @include m.laptop {
          margin-left: 5rem;
          // font-size: 2rem;
          margin-left: 2rem;
        }
        @include m.desktop {
          // font-size: 2rem;
        }
        @include m.desktopXL {
          // font-size: 2rem;
        }
        // font-family: v.$textNarrow;
      }
    }
    .form-info-social-networks {
      margin: 2rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      @include m.mobile {
        flex-direction: row;
        justify-content: center;
      }
      .form-social-text {
        color: v.$white;
        font-size: 2.5rem;
        margin-bottom: 2rem;
        @include m.laptop {
          margin-right: 3rem;
          font-size: 2.5rem;
          margin-bottom: unset;
        }
        @include m.desktopXL {
          font-size: 2.5rem;
        }
        // font-family: v.$textNarrow;
      }
      .form-social-icons {
        display: flex;
        margin-bottom: 3rem;
        @include m.laptop {
          margin-bottom: 0;
        }
        .img-container {
          height: 3rem;
          margin: auto 1rem;
          img {
            height: 100%;
          }
        }
      }
    }
    .footer-3 {
      display: flex;
      justify-content: center;
      @include m.tablet {
        align-items: flex-end;
        justify-content: flex-end;
        grid-column: 1 / 3;
      }
      @include m.desktop {
        grid-column: auto;
      }
    }
  }
  a {
    transition: color 0.5s;
    &:hover {
      color: darken($color: v.$primaryBlue, $amount: 20);
      transition: color 0.5s;
    }
  }
}
