@use "base/variables" as v;
@use "base/mixins" as m;

.program {
  .program-info {
    margin: inherit 2rem;
    h3 {
      padding: 2rem 2rem 0 2rem;
      text-align: center;
      color: v.$primaryBlue;
    }
    @include m.tablet {
      h3 {
        vertical-align: sub;
        span {
          vertical-align: middle;
          font-size: 1.5em;
          padding-left: 1rem;
          padding-right: 1rem;
        }
      }
      margin: inherit auto 5rem;
    }
    @include m.desktop {
      h3 {
        span {
          font-size: 1.25em;
        }
      }
      margin: 3rem auto 7rem;
      p {
        margin: inherit 20rem;
      }
    }
    @include m.desktopXL {
      p {
        margin: inherit 30rem;
      }
    }
  }
}

.program-details {
  .program-details-text {
    .program-details-title,
    .program-details-heart {
      display: none;
    }
    background-color: v.$primaryBlueDark;
    width: 100%;
    padding: 3rem 2rem;
    @include m.tablet {
      position: relative;
      .program-details-heart {
        display: block;
        position: absolute;
        top: 3rem;
        left: 5rem;
        width: 4rem;
        img {
          width: 100%;
        }
        @include m.laptop {
          left: 7rem;
          top: 2.5rem;
          width: 5rem;
        }
        @include m.desktop {
          left: 8rem;
          top: 3.5rem;
          width: 6rem;
        }
      }
      .program-details-title {
        display: block;
        position: absolute;
        background-color: v.$secondaryGray;
        top: -5rem;
        padding: 2rem;
        border-radius: 5rem;
        color: v.$primaryBlueDark;
        p {
          margin: 0 1rem;
          text-align: left;
          span {
            display: block;
            text-align: right;
            font-size: 1.5em;
            padding-left: 3rem;
          }
        }
        @include m.laptop {
          top: -4rem;
          left: 4rem;
          p {
            text-align: center;
            span {
              display: inline;
              font-size: 1.8em;
              padding-left: 1rem;
              vertical-align: middle;
            }
          }
        }
        @include m.desktop {
          padding-left: 5rem;
          padding-right: 5rem;
        }
      }
      width: 80%;
      margin: auto auto 5rem;
      padding-top: 7rem;
      margin-top: 7rem;
    }
    @include m.desktop {
      padding-left: 10rem;
      padding-right: 10rem;
      padding-top: 10rem;
      margin-top: 10rem;
    }
  }
  ol {
    width: 100%;
    padding-left: 0;
    li {
      list-style: decimal;
      padding: 1rem;
    }
  }
  ul {
    li {
      list-style: disc;
      list-style-position: outside;
      text-align: left;
    }
  }
  li {
    width: 100%;
    color: v.$white;
    list-style-position: inside;
    text-align: center;
  }
}

.login-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  a {
    color: v.$white;
    background-color: v.$primaryBlueDark;
    padding: 1rem 2rem;
    border-radius: 3rem;
    margin-bottom: 3rem;
  }
  p {
    margin-left: 5rem;
    margin-right: 5rem;
    @include m.tablet {
      margin-left: 15rem;
      margin-right: 15rem;
      margin-bottom: 2rem;
    }
    @include m.desktop {
      margin-left: 30rem;
      margin-right: 30rem;
      margin-bottom: 4rem;
    }
  }
}
