@use "base/variables" as v;
@use "base/mixins" as m;

.hero-img {
    width: 100%;
    img {
        width: 100%;
    }
    position: relative;
    .hero-text {
        position: absolute;
        height: 100%;
        margin-left: 50%;
        margin-right: 10%;
        bottom: 0;
        color: v.$white;
        display: flex;
        flex-direction: column;
        justify-content: center;
        p {
            color: v.$white;
            margin: 0;
            font-size: 1rem;
            @include m.mobile {
                font-size: 1.3rem;
            }
            @include m.tablet {
                font-size: 1.5rem;
                text-align: left;
                span {
                    font-size: 1.2em;
                    display: block;
                    padding-left: 1.5rem;
                }
            }
            @include m.laptop {
                font-size: 2rem;
                text-align: left;
                span {
                    font-size: 1.5em;
                    display: block;
                    padding-left: 1.5rem;
                }
            }
            @include m.desktop {
                font-size: 2.75rem;
                text-align: left;
                span {
                    font-size: 1.5em;
                    display: block;
                    padding-left: 1.5rem;
                }
            }
            @include m.desktopXL {
                font-size: 3rem;
                text-align: left;
                span {
                    font-size: 1.5em;
                    display: block;
                    padding-left: 1.5rem;
                }
            }
        }
        a {
            margin-top: 1rem;
            border: 1px solid v.$white;
            padding: 0.5rem 1rem;
            margin-left: auto;
            margin-right: auto;
            &:hover {
                color: v.$white;
                background-color: v.$primaryBlue;
            }
            @include m.mobile {
                font-size: 1.3rem;
                margin-top: 2rem;
            }
            @include m.tablet {
                font-size: 1.5rem;
                margin-top: 3rem;
            }
            @include m.laptop {
                font-size: 2rem;
                margin-top: 6rem;
            }
            @include m.desktop {
                font-size: 2.5rem;
                margin-top: 10rem;
                padding: 1rem 3rem;
            }
            @include m.desktopXL {
                font-size: 3rem;
                margin-top: 15rem;
                padding: 1rem 5rem;
            }
        }
    }
}

.hero-button {
    display: flex;
    justify-content: center;
}