@use "base/variables" as v;
@use "base/mixins" as m;

.background-image {
  position: fixed;
  z-index: -1;
  margin-top: 0rem;
  img {
    width: 100%;
  }
}

.header-top {
  display: flex;
  align-items: center;
  justify-content: center;
  @include m.mobile {
    justify-content: space-between;
  }
  height: 5rem;
  background-color: v.$secondaryGray;
  p {
    font-size: 1.2rem;
    margin: 0;
    color: v.$gray;
    @include m.desktop {
      font-size: 1.5rem;
      padding-left: 1rem;
    }
  }
}

.header-phone {
  display: none;
  align-items: center;
  @include m.mobile {
    display: flex;
  }
  & > * {
    margin: 1rem;
  }
  .img-container {
    height: 2rem;
    padding-left: 1rem;
    @include m.laptop {
      padding-left: 3rem;
    }
    img {
      height: 100%;
    }
  }
}

.header-social-media {
  display: flex;
  align-items: center;
  .img-container {
    height: 2rem;
    margin: 1.5rem;
    @include m.laptop {
      margin: 3rem;
    }
    &:last-child {
      padding-right: 1rem;
      @include m.laptop {
        padding-right: 2rem;
      }
    }
    img {
      height: 100%;
    }
  }
  .mail {
    // transition: all 0.25s;
    // &:hover {
    //   filter: brightness(0) invert(1) invert(59%) sepia(35%) saturate(3613%) hue-rotate(155deg) brightness(86%) contrast(80%);;
    //   transition: all 0.25s;
    // }
  }
}

.header-middle {
  height: 15rem;
  @include m.laptop {
    height: 20rem;
  }
  background-color: v.$primaryBlueDark;
  display: flex;
  align-items: center;
  justify-content: center;
  .img-container {
    height: 2.5rem;
    @include m.mobile {
      height: 3rem;
    }
    @include m.laptop {
      height: 4rem;
    }
    @include m.desktop {
      height: 5rem;
    }
    img {
      height: 100%;
    }
  }
}

.container-fluid {
  display: flex;
  justify-content: center;
}

.navbar {
  background-color: v.$white;
  color: v.$primaryBlueDark;
  font-family: v.$text;
  padding-bottom: 0;
  padding-top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  .container-fluid {
    padding: 0;
  }
  .navbar-toggler {
    margin: 1rem auto;
    padding: 0.75rem;
  }
}

@include m.laptop {
  .navbar-expand-md .navbar-collapse {
    display: block !important;
  }
}
.navbar-toggler:focus {
  box-shadow: none;
}

.header-bottom {
  width: 100%;
  height: auto;
  @include m.laptop {
    height: 7rem;
  }
  @include m.desktop {
    height: 10rem;
  }
  display: flex;
  align-items: center;
  .navbar {
    padding: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    @include m.laptop {
      flex-direction: row;
    }
    justify-content: space-evenly;
    align-items: center;
    .navbar-element {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      @include m.laptop {
        width: 20%;
        height: 7rem;
      }
      @include m.desktop {
        height: 10rem;
      }
      .img-container {
        height: 2rem;
        @include m.laptop {
          height: 2rem;
        }
        @include m.desktop {
          height: 3rem;
        }
        img {
          height: 100%;
        }
      }
      p {
        // font-family: v.$textNormal;
        font-size: 1.5rem;
        margin: 1.35rem;
        @include m.laptop {
          font-size: 1.8rem;
        //   margin: 1rem;
          margin-right: 0;
        }
        @include m.desktop {
          font-size: 2.25rem;
        }
        @include m.desktopXL {
          font-size: 2.5rem;
          margin: 2rem;
        }
      }
    }
    a {
      text-decoration: none;
      //   color: v.$secondaryGray;
      &:hover {
        color: lighten($color: v.$primaryBlueDark, $amount: 15);
      }
    }
  }
}
