@use "base/variables" as v;
@use "base/mixins" as m;

.home-info {
  width: 80%;
  margin: 2rem auto;
  .img-container {
    width: 17rem;
    margin: 3rem auto 0 auto;
    img {
      width: 100%;
    }
  }
  @include m.mobile {
    .img-container {
      width: 20rem;
    }
    margin: 4rem auto;
  }
  @include m.tablet {
    margin: 6rem auto;
  }
  @include m.desktop {
    .img-container {
      width: 30rem;
    }
    margin: 10rem auto;
  }
  @include m.desktopXL {
    margin: 15rem auto;
  }
  .heart-container {
    width: 8.5rem;
    margin: auto;
    margin-bottom: 2rem;
    @include m.mobile {
      width: 10rem;
    }
    img {
      padding: 2rem;
      padding-top: 0;
      width: 100%;
    }
  }
  .home-info-text {
    @include m.tablet {
      margin-bottom: 4rem;
    }
    @include m.laptop {
      margin-top: 7.5rem;
      margin-bottom: 7.5rem;
    }
  }
}

.home-section {
  background-color: v.$primaryBlue;
  text-align: right;
  padding: 2rem;
  color: v.$white;
  span {
    font-size: 1.25em;
  }
  @include m.tablet {
    padding-right: 7.5rem;
  }
}

.home-programs {
  .home-section-program-1,
  .home-section-program-2 {
    display: grid;
    .home-program-img {
      order: -1;
      width: 100%;
      img {
        width: 100%;
      }
    }
    @include m.tablet {
      margin-bottom: 5rem;
    }
  }
  .home-program-text {
    background-color: v.$secondaryGray;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 3rem auto;
    .home-program-description {
      padding: inherit 2rem;
      @include m.desktop {
        margin-top: 3rem;
        margin-bottom: 3rem;
      }
      @include m.desktopXL {
        margin-top: 7rem;
        margin-bottom: 7rem;
      }
      h3 {
        color: v.$primaryPurple;
        text-align: center;
        padding-top: 3rem;
      }
      p {
        color: v.$white;
      }
    }
    .home-program-btn {
      color: v.$white;
      border: 1px solid v.$white;
      padding: 1rem 4rem;
      &:hover {
        background-color: darken($color: v.$secondaryGray, $amount: 5);
      }
      margin-bottom: 3rem;
      @include m.tablet {
        margin-top: 3rem;
      }
    }
  }
  @include m.tablet {
    margin: 5rem 7rem 7rem 7rem;
  }
  @include m.laptop {
    width: 60rem;
    margin: 20rem auto 10rem auto;
    .home-section-program-1,
    .home-section-program-2 {
      width: 65rem;
      margin-top: 15rem;
      position: relative;
      .home-program-img {
        order: 0;
        width: 27.5rem;
        img {
          width: 100%;
        }
      }
      .home-program-text {
        display: grid;
        h3 {
          margin: 2rem;
          padding: 0;
        }
        p {
          margin: 2rem;
          padding: 0;
        }
        .home-program-btn {
          width: 10rem;
          padding: 0.5rem 1rem;
          text-align: center;
          margin: 35% auto 5% auto;
        }
      }
    }
    .home-section-program-1 {
      .home-program-img {
        position: absolute;
        left: -3.5rem;
        top: -11rem;
        .img-rect-1 {
          width: 100%;
          height: 100%;
          background-color: v.$primaryBlueDark;
          position: relative;
          .img-rect-2 {
            position: absolute;
            right: 2rem;
            width: 100%;
            height: 100%;
            background-color: v.$primaryBlueDark;
          }
        }
      }
      .home-program-text {
        .home-program-btn {
          order: -1;
        }
        grid-template-columns: 3fr 5fr;
      }
    }
    .home-section-program-2 {
      .home-program-img {
        position: absolute;
        top: -11rem;
        right: -3.5rem;
      }
      .home-program-text {
        grid-template-columns: 5fr 3fr;
      }
    }
  }
  @include m.desktop {
    width: 80%;
    .home-section-program-1 {
      .home-program-img {
        left: -1.5rem;
        top: -15rem;
      }
      .home-program-text {
        .home-program-btn {
          order: -1;
        }
        grid-template-columns: 3fr 5fr;
      }
    }
    .home-section-program-2 {
      .home-program-img {
        position: absolute;
        top: -15rem;
        right: -1.5rem;
      }
      .home-program-text {
        grid-template-columns: 5fr 3fr;
      }
    }
    .home-section-program-1,
    .home-section-program-2 {
      margin-top: 20rem;
      width: 100%;
      .home-program-img {
        width: 40%;
      }
    }
    .home-program-btn {
      width: 15rem!important;
      font-size: 2rem;
    }
  }
  @include m.desktopXL {
    .home-section-program-1,
    .home-section-program-2 {
      .home-program-img {
        top: -80%;
      }
    }
    .home-section-program-1,
    .home-section-program-2 {
      margin-top: 30rem;
    }
  }
}

.grid-button {
  margin: 1rem inherit 5rem inherit;
  a {
    font-size: 1.4rem;
    @include m.tablet {
      font-size: 1.6rem;
    }
  }
  @include m.tablet {
    margin-top: 4rem;
  }
  @include m.laptop {
    height: 100%;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    padding-bottom: 2rem;
  }
}

.home-section-program-1 {
  .grid-button {
    order: -1;
  }
}

.home-program-text {
  @include m.desktop {
    h3 {
      font-size: 2.5rem;
    }
    p {
      font-size: 1.6rem;
    }
  }
  @include m.desktopXL {
    h3 {
      font-size: 2.75rem;
    }
    p {
      font-size: 2rem;
    }
  }
}
