@use "variables" as v;
@use "base/mixins" as m;

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  line-height: 1.5;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
}

h2 {
  font-size: 2rem;
  @include m.mobile {
    font-size: 2.25rem;
  }
  @include m.tablet {
    font-size: 2.5rem;
  }
  @include m.laptop {
    font-size: 2.75rem;
  }
  @include m.desktop {
    font-size: 3rem;
  }
  @include m.desktopXL {
    font-size: 3.5rem;
  }
}

h3 {
  font-size: 1.5rem;
  @include m.mobile {
    font-size: 1.75rem;
  }
  @include m.tablet {
    font-size: 1.85rem;
  }
  @include m.desktop {
    font-size: 3rem;
  }
  @include m.desktopXL {
    font-size: 3.5rem;
  }
}

main {
  p {
    font-size: 1.2rem;
    text-align: center;
    margin: 3rem 0rem;
    color: v.$primaryBlueDark;
    font-family: v.$textSemiBold;
    @include m.mobile {
      font-size: 1.4rem;
    }
    @include m.tablet {
      font-size: 1.5rem;
      margin: 3rem 2rem 0;
    }
    @include m.laptop {
      margin: 3rem 5rem 0;
    }
    @include m.desktop {
      font-size: 2rem;
    }
    @include m.desktopXL {
      font-size: 2.25rem;
      margin: 3rem 10rem 1rem;
    }
  }
}

footer {
  p {
    // font-family: v.$textNormal;
    font-size: 1.2rem;
    @include m.mobile {
      font-size: 1.4rem;
    }
    @include m.tablet {
      font-size: 1.5rem;
    }
    @include m.desktop {
      font-size: 2rem;
    }
    @include m.desktopXL {
      font-size: 2.25rem;
    }
  }
}

ul {
  padding-left: 0;
  p {
    padding-left: 2rem;
    padding-right: 2rem;
  }
  @include m.laptop {
    font-size: 2rem;
  }
}

li {
  list-style: none;
  font-size: 1.2rem;
  @include m.mobile {
    font-size: 1.4rem;
  }
  @include m.tablet {
    font-size: 1.5rem;
  }
  @include m.desktop {
    font-size: 1.7rem;
  }
  @include m.desktopXL {
    font-size: 1.9rem;
  }
}

form {
  p {
    font-size: 1.1rem;
    @include m.mobile {
      font-size: 1.35rem;
    }
    @include m.tablet {
      font-size: 1.45rem;
    }
    @include m.desktop {
      font-size: 1.65rem;
    }
    @include m.desktopXL {
      font-size: 1.85rem;
    }
  }
}

a {
  font-size: 1.25srem;
  text-decoration: none;
  color: unset;
  @include m.desktopXL {
    font-size: 1.5rem;
  }
}

footer {
  a, p {
    font-size: 1.25srem;
    text-decoration: none;
    color: unset;
    @include m.desktopXL {
      font-size: 1.5rem;
    }
  }
}

.table {
  width: 90%;
  margin: auto;
  @include m.tablet {
    width: 70%;
  }
  @include m.desktop {
    width: 60%;
  }
  @include m.desktopXL {
    width: 50%;
  }
}

td {
  font-size: 1.1rem;
  @include m.tablet {
    font-size: 1.25rem;
  }
  @include m.desktop {
    font-size: 1.5rem;
  }
}

tr {
  vertical-align: middle;
}

img {
}

footer {
}

button,
.btn {
  font-size: 1.25rem;
  @include m.mobile {
    font-size: 1.5rem;
  }
  @include m.laptop {
    // font-size: 1.5rem;
  }
  @include m.desktop {
    // font-size: 1.5rem;
  }
  @include m.desktopXL {
    // font-size: 1.5rem;
  }
}

.directory-btn {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  @include m.desktopXL {
    align-items: flex-start;
  }
}

.form-select,
.form-control,
.invalid-feedback {
  font-size: 1rem;
  @include m.mobile {
    font-size: 1.25rem;
  }
  @include m.laptop {
    font-size: 1.75rem;
  }
  @include m.desktop {
    // font-size: 0.75rem;
  }
  @include m.desktopXL {
    // font-size: 1.5rem;
  }
}

label {
  font-size: 1.5rem;
  @include m.mobile {
    font-size: 1.5rem;
  }
  @include m.laptop {
    font-size: 2rem;
  }
  @include m.desktop {
    // font-size: 0.75rem;
  }
  @include m.desktopXL {
    // font-size: 1.5rem;
  }
}
